import React, { Component } from 'react';
import logo from './assets/solar.svg';

import './App.css';
import planets from './data/data.json';
import Modal from 'react-modal';
import API from './utils/API.js';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    width: "50%",
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    zIndex: 1,
    backgroundColor: '#333333',
    borderColor: '#333333'
  },
  overlay: {
    zIndex: 2,
    backgroundColor: 'rgba( 0, 0, 0,.75)'
  }
};
const c_ms = 299792458;
const appElement = document.getElementById('App');

Modal.setAppElement(appElement);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      planets: {},
      names: ["Mercury", "Venus", "Earth", "Mars", "Jupiter", "Saturn", "Uranus", "Neptune", "Pluto", "Halley", "Eris"],
      zoom: 30,
      modalIsOpen: false,
      name: 'Saturn',
      symbol: '',
      currentR: 0
    };
    this.openModal = this.openModal.bind(this);
    this.afterOpenModal = this.afterOpenModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  componentDidMount() {
    this.GR();
  }

  openModal(planet) {
    this.setState({ name: planet })
    this.setState({ modalIsOpen: true });
  }


  afterOpenModal() {
    // references are now sync'd and can be accessed.
    this.subtitle.style.color = 'white';
  }

  closeModal() {
    this.setState({ modalIsOpen: false });
  }

  radius(planet) {
    this.meanAnom(planet);
    let r = planet.a_km * (1 - planet.e * Math.cos(planet.E));
    this.setState({ curretR: r })
  }

  heliocentricLong(planet) {
    let t = new Date();
    let tnow = t.getTime();
    let Δτ = ((tnow / 1000) - 1535760000)
    let Δτ2 = ((tnow / 1000) - 946728000)
    planet.θ2 = ((planet.ε) * (Δτ2 / planet.T) + planet.periangle) * (Math.PI / 180);
    planet.θ = ((360 + planet.ε) * (Δτ / planet.T) + planet.helioLong) * (Math.PI / 180);
  }

  precession(planet) {
    planet.ε = 24 * (Math.PI ** 2) * 180 * ((planet.a_km * 1000) ** 2) / ((planet.T ** 2) * (c_ms ** 2) * (1 - planet.e ** 2));
    //console.log(planet.ε * 3600 * planets.data[2].T/planet.T * 100)
  }

  eccentricity(planet) {
    planet.b = planet.a_km * Math.sqrt(1 - planet.e ** 2)
    planet.ax = (planet.a_km / (5* 10 ** 6)) * 2
    planet.by = ((planet.b) / (5*10 ** 6)) * 2
    //console.log(Math.sqrt(1-planet.by**2/planet.ax**2))
    //planet.foci = //Math.sqrt(planet.a_km ** 2 - planet.b ** 2)
    planet.foci = planet.peri/planet.a_km *50
    //console.log(planet.foci)
  }

  meanAnom(planet) {
    let t = new Date();
    let tnow = t.getTime();//85000 on 4/26 //-600000~-670000 on 5/5//550000 on 5/19 ~500000 on 5/24//
    let Δτ = ((tnow / 1000) - planet.τ)
    this.precession(planet);
    planet.Δε = (Δτ / planet.T) * planet.ε
    planet.M = (2 * Math.PI + planet.ε * (Math.PI / 180)) * (Δτ / planet.T);
    planet.E = planet.M + (planet.e - (planet.e ** 3) / 8) * Math.sin(planet.M) + .5 * planet.e ** 2 * Math.sin(2 * planet.M) + (3 / 8) * (planet.e ** 3) * Math.sin(3 * planet.M);
  }

  GR() {

    let obj = {
      Mercury: {},
      Venus: {},
      Earth: {},
      Mars: {},
      Jupiter: {},
      Saturn: {},
      Uranus: {},
      Neptune: {},
      Pluto: {},
      Halley: {},
      Eris: {}
    }
    planets.data.forEach(planet => {
      this.eccentricity(planet);
      //<div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Relativistic Apsidal Precession: { Math.round(this.state.planets[this.state.name].ε * 1000)/1000 + " arcseconds/century"}</div>
      this.meanAnom(planet);
      let r = planet.a_km * (1 - planet.e * Math.cos(planet.E));
      let rScreen = r / (5*10 ** 6);
      this.heliocentricLong(planet)
      let x = rScreen * Math.cos(planet.θ) + 50;
      let y = rScreen * Math.sin(planet.θ) + 50;
      console.log(443677964.9/5958614836.1)
      let ty = (planet.T-planet.T%31558149.504)/31558149.504
      let td = (planet.T%31558149.504)/86400
      let parta = (50 + "% ");
      let partb = (50) + (planet.foci / planet.a_km) + "%";
      //console.log(planet.θ2 * (180 / Math.PI))
      obj[planet.name] = {
        a: planet.a_km,
        helio: Math.round(planet.θ * (180 / Math.PI)),
        M: "rotate(" + (planet.θ2 * (180 / Math.PI) - 180) + "deg)",
        r: Math.round(r),
        rau: Math.round(r/149598073 * 1000),
        ang: planet.angMo,
        ε: planet.ε * 3600 * planets.data[2].T/planet.T * 100,
        angexp: planet.angMo_exp,
        day: Math.round(td),
        year: Math.round(ty),
        mass: planet.massKg,
        massexp: planet.mass_exp,
        X: x + "%",
        Y: y + "%",
        symbol: planet.symbol,
        perihelion: planet.date,
        Ax: planet.ax + "%",
        By: planet.by + "%",
        t: (50 - planet.by / 2) + "%",
        l: (50 - planet.ax / 2) + (Math.sqrt(planet.a_km ** 2 - planet.b ** 2) / (5*10**6)) + "%",
        foci: planet.foci + parta
      }
    });
    this.setState({ planets: obj });
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Solar Sim</h1>
          <div className="zoom">
            <button className="zoombutton" onClick={() => this.setState({ zoom: (this.state.zoom < 45) ? this.state.zoom + 5 : this.state.zoom })}>Zoom In</button>
            <button className="zoombutton" onClick={() => this.setState({ zoom: (this.state.zoom > 0) ? this.state.zoom - 5 : this.state.zoom })}>Zoom Out</button>
          </div>
        </header>
        <div>
          <Modal
            isOpen={this.state.modalIsOpen}
            onAfterOpen={this.afterOpenModal}
            onRequestClose={this.closeModal}
            style={customStyles}
            contentLabel="Example Modal"
          >
          <div className="top">
            <h2 ref={subtitle => this.subtitle = subtitle}> <span style={{fontSize: "2em"}}>{(typeof this.state.planets['Mercury'] !== "undefined") ? this.state.planets[this.state.name].symbol : null}</span> {this.state.name} Fact Sheet</h2>
            <button className="close" onClick={this.closeModal}>⛌</button>
            </div>
            <div className="contain">
            <div>
              <img className="images" alt="logo" style={{ margin: 'auto' }} src={require("./assets/" + this.state.name + ".jpg")} width="300" />
            </div>
            {(typeof this.state.planets['Mercury'] !== "undefined") ?
              <div className="facts">
                <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Distance from Sun: {this.state.planets[this.state.name].r + " km ("}{this.state.planets[this.state.name].rau /1000 + " au)"}</div>
                <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Sidereal Period: {this.state.planets[this.state.name].year + " years " + this.state.planets[this.state.name].day + " days"}</div>
                <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Mass: {this.state.planets[this.state.name].mass + " ⋅ 10"} <sup> {this.state.planets[this.state.name].massexp} </sup> {" kg"}</div>
                <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}> Semimajor Axis: {this.state.planets[this.state.name].a + " km ("}{Math.round(this.state.planets[this.state.name].a/149598073 * 1000)/1000 + " au)"}</div>
                { this.state.name === "Eris" ? null : this.state.name === "Halley" ? null : <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Heliocentric Longitude: {this.state.planets[this.state.name].helio + "°"}</div>}
                { this.state.planets[this.state.name].ang !== "" ? <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Orbital Angular Momentum: {this.state.planets[this.state.name].ang + " ⋅ 10"} <sup> {this.state.planets[this.state.name].angexp} </sup> {" kg⋅km/day"}</div>: null}
                <div style={{ color: 'white', marginTop: '5px', marginBottom: '10px'}}>Perihelion: {this.state.planets[this.state.name].perihelion}</div>
              </div>
              
              : null}
              </div>
          </Modal>
        </div>
        <div className="square" style={{ width: this.state.zoom + '%' }}>
          <div className="content">
          <img id="kupier" alt="logo" src={require("./assets/kupier.svg")}/>
          <img id="asteroid" alt="logo" src={require("./assets/asteroid2.svg")}/>
            {(typeof this.state.planets['Mercury'] !== "undefined") ? this.state.names.map(planet =>
              <div className="orbit" key={planet} value={planet} style={{
                transform: this.state.planets[planet].M,
                transformOrigin: this.state.planets[planet].foci, top: this.state.planets[planet].t, left: this.state.planets[planet].l, width: this.state.planets[planet].Ax, height: this.state.planets[planet].By
              }}>{this.state.planets[planet].by}</div>) : null}
            {(typeof this.state.planets['Mercury'] !== "undefined") ? this.state.names.map(planet =>
              <div className="planet" key={planet} style={{ left: this.state.planets[planet].X, top: this.state.planets[planet].Y }} onClick={() => this.openModal(planet)}>{planet}</div>) : null}
            <div id="sun"></div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;



